body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

h1 {
  font-size: 64px;
  font-weight: 500;
}

.image-container {
  height: 100vh;
  width: 100%;
  background: beige;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.text-container {
  position: relative;
  background: blue;
  overflow: hidden;
  position: relative;
  width: 64px;
}

.text {
  background: salmon;
  display: inline-block;
  width: 350px;
}

.cursor {
  position: absolute;
  height: 100%;
  aspect-ratio: 1;
  background: white;
  right: 0;
  top: 0;
}